import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import { toggleUnderline } from '../actions';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

const isDisabled = (controller: Controller) => {
  const { isCollapsedAtEdge } = controller.value.selection;
  const disabled = isCollapsedAtEdge;
  return Boolean(disabled);
};

export function MobileUnderlineButton(props: MobileToolbarButtonProps) {
  const { controller, item, Wrapper } = props;
  const isActive = controller.query('isUnderline');

  const handleClick = React.useCallback(
    () => controller.run('onAction', toggleUnderline()),
    [controller],
  );

  return (
    <Wrapper
      item={item}
      type="horiz"
      icon="underline"
      isActive={isActive}
      isDisabled={isDisabled(controller)}
      onClick={handleClick}
    />
  );
}

export function MobileUnderlineBigButton(props: MobileToolbarButtonProps) {
  const { controller, item, Wrapper } = props;
  const isActive = controller.query('isUnderline');

  const handleClick = React.useCallback(
    () => controller.run('onAction', toggleUnderline()),
    [controller],
  );

  return (
    <Wrapper
      item={item}
      type="big"
      icon="underline"
      isFocused={isActive}
      isDisabled={isDisabled(controller)}
      onClick={handleClick}
    />
  );
}
